<template lang="pug">
  .line-chart__content
    line-chart(
       :labels="labels"
       :data='chartdata'
       :options='options'
    )
</template>

<script>
  import LineChart from './chartScholars.js'
  export default {
    components: {
      LineChart
    },
    props: {
      chartData: Array
    },
    data () {
      return {
        datacollection: {},
        options : {
            scales: {
                yAxes: [{
                    ticks: {
                        min: 0,
                        stepSize: 1
                    }
                }]
            }    
        }   
      }
    },
    async mounted(){
        await this.fillData()
    },
    computed: {
      labels: function(){ 
        return this.chartData.map(ele=> ele.date)
      },
      chartdata: function(){ 
        return this.chartData.map(ele=> ele.count)
      }
    },
    methods: {
      fillData(){
        this.datacollection = {
          labels: this.labels,
          datasets: [
            {
            label: "Количество учеников",
            fill: false,
            backgroundColor: [
                '#346f79',
                '#fead00',
                '#80000d',
                '#555555',
                '#bf3300',
                '#b97b2a',
                '#6e3b00',
                '#171442', 
                '#e6000a',
                '#a43cbe',
                '#226ab7',
                '#ff7600',
                '#00b229',
                '#925148',
                '#7f7f7f'
            ],
            data: this.chartdata,
            borderColor: '#000',
            borderWidth: 2,
            pointRadius: 5
            }
          ]
        }
        this.loaded = true
      }
    },

  }
</script>

<style lang="scss">
  .line-chart__content {
    width: 100%;
    background: #ffe9ce;
    border-radius: 6px;
    padding: 3%;
  }
</style>