import { Line, mixins } from 'vue-chartjs'
const { reactiveProp } = mixins

export default {
  extends: Line,
  mixins: [reactiveProp],
  props: ['data','labels','options'],
  mounted () {
    // this.chartData is created in the mixin.
    // If you want to pass options please create a local options object
    this.renderLineChart();
  },
  computed: {
    chartData: function() {
      return this.data;
    }
  },
  methods: {
    renderLineChart: function() {
    this.renderChart({
      labels: this.labels,
      datasets: [
        {
        label: "Количество учеников",
        fill: false,
        backgroundColor: [
            '#346f79',
            '#fead00',
            '#80000d',
            '#555555',
            '#bf3300',
            '#b97b2a',
            '#6e3b00',
            '#171442', 
            '#e6000a',
            '#a43cbe',
            '#226ab7',
            '#ff7600',
            '#00b229',
            '#925148',
            '#7f7f7f'
        ],
        data: this.chartData,
        borderColor: '#7f7f7f',
        borderWidth: 2,
        pointRadius: 5
        }
      ]
    }, this.options)
  }
  },  
  watch: {
    data: function() {
      this.renderLineChart();
    }
  }
}